import { Button, Modal, Input, Space, message } from 'antd';
import { useState, useEffect } from 'react';
import Timer from './Timer';
import { Link, useNavigate } from "react-router-dom";
import ReactPlayer from 'react-player/youtube';

const PassCode = () => {
    const [pAss, setPass] = useState('');
    const [sLetter, setSletter] = useState('');
    const [tLetter, setTletter] = useState('');
    const [vIdeoflag, setVideoflag] = useState(false);

    const HandleChange = (param) => {
        setPass(param)
    }
    const navigate = useNavigate();
    useEffect(() => {
        if (!localStorage.getItem('user')) {
            navigate("/osp");
        }
    }, []);
    let messageKey = null;
    const onClose = () => {
        message.destroy(messageKey);
    };
    const Confirm = () => {
        if (!pAss) {
            message.config({
                top: "60px"
            })
            message.warning("Invalid password", 25, onClose)
        }

        else if (pAss === "DrainsDartsP0!ato") {
            message.success("Success", onClose)
            setVideoflag(true);
        }
        else {
            message.warning("Failure, Please input password correctly again", 25, onClose)
        }

    }

    return (
        <div className="bg-gray-800 mt-5">
            <Timer />
            <div className="bg-gray-800  mt-60 h-[90vh]">
                <h3 className='mb-9'>Enter your three word passcode to save the data</h3>
                <div className="pin-pad-container grid-cols-4">
                    <Space className='mb-5' direction="vertical">
                        <Input.Password placeholder="Enter password" onChange={(event) => HandleChange(event.target.value)} />
                    </Space>
                    <Button type="primary" onClick={Confirm}>Confirm</Button>
                </div>
                <Modal
                    title="Video"
                    centered
                    okButtonProps={{ style: { backgroundColor: 'black' } }}
                    cancelButtonProps={{ style: { backgroundColor: 'black', color: 'white', border: "none" } }}
                    cancelText="Close"
                    open={vIdeoflag}
                    onOk={() => {
                        setVideoflag(false);
                        document.getElementById('downloaded-video').pause();
                    }}
                    onCancel={() => {
                        setVideoflag(false);
                        document.getElementById('downloaded-video').pause();
                    }}
                >
                    {/* <video className="text-center"
                        autoPlay
                        id="downloaded-video"
                        controls
                    >
                        <source
                            src="https://youtu.be/p0Q9L67XpNU"
                            type="video/mp4"
                        />
                    </video> */}
                    <ReactPlayer
                        url="https://youtube.com/shorts/pjFhKhGT_lo"
                        playing
                        controls
                        width="100%"
                        height="100%"
                    />
                </Modal>
            </div>
        </div>
    )
}
export default PassCode;