import { Routes, Route, Navigate } from "react-router-dom";
import { useSelector, useDispatch } from 'react-redux'
import Header from "./component/Header/Header";
import Footer from "./component/Footer/Footer";
import Signup from "./component/App/User/Signup";
import Login from "./component/App/User/Login";
import Pincode from "./component/App/User/Pincode";
import Email from "./component/App/Email";
import PassCode from "./component/App/PassCode";
import Explore from "./component/App/Option/Explore"
import ORusb from "./component/App/Option/ORusb"
import B3 from "./component/App/Option/B3";
import Down from "./component/App/Down";
import QRCode from "./component/App/QRCode";
import Timer from "./component/App/Timer";
import Risk from "./component/App/Risk";

const ARoute = () => {
    const { Gameover } = useSelector((state) => state.InputValue);

    return (
        <>
            {Gameover ? <>
                <Header />
                <Routes >
                    <Route path="/" element={<Navigate to="/osp" />}>
                    </Route>
                    <Route path="/osp/signup" element={<Signup />} />
                    <Route path="/osp/login" element={<Login />} />
                    <Route path="/osp" element={<Login />} />
                    <Route path="/osp/pincode" element={<Pincode />} />
                    <Route path="/osp/email" element={<Email />} />
                    <Route path="/osp/passcode" element={<PassCode />} />
                    <Route path="/osp/b3" element={<B3 />} />
                    <Route path="/osp/openrunusb" element={<ORusb />} />
                    <Route path="/osp/explore" element={<Explore />} />
                    <Route path="/osp/down" element={<Down />} />
                    <Route path="/osp/qrcode" element={<QRCode />} />
                    <Route path="/osp/timer" element={<Timer />} />
                    <Route path="/osp/risk" element={<Risk />} />
                </Routes>
                {/* <Footer /> */}
            </> : <>
                <Routes >
                    <Route path="/" element={<Navigate to="/osp" />} />
                    <Route path="/osp/risk" element={<Risk />} />
                    <Route path="/osp/signup" element={<Signup />} />
                    <Route path="/osp/login" element={<Login />} />
                </Routes>
                {/* <Footer /> */}
            </>}


        </>
    )
}
export default ARoute;