import { Button, Modal, Input, Space, message } from 'antd';
import React, { useEffect, useRef, useState } from "react";
import ReactPlayer from 'react-player/youtube';
import { Link, useNavigate } from "react-router-dom";
import Timer from './Timer';

const Down = () => {
    const navigate = useNavigate();
    const videoRef = useRef(null);
    const [pAss, setPass] = useState('');
    const [vIdeoflag, setVideoflag] = useState(false);
    const [timerflag, setTimerflag] = useState(false);
    const [pAssflag, setPassflag] = useState(false);
    useEffect(() => {
        if (!localStorage.getItem('user')) {
            navigate("/osp");
        }
    }, []);
    const onDownload = () => {
        setVideoflag(true);
        setTimerflag(true);
        if (videoRef.current) {
            videoRef.current.play();
        }
    };

    const HandleChange = (param) => {
        setPass(param);
    };
    let messageKey = null;
    const onClose = () => {
        message.destroy(messageKey);
    };
    const ConfirmPassword = async () => {
        if (pAssflag === true) {
            setVideoflag(false);
            document.getElementById('downloaded-video').pause();
            if (videoRef.current) {
                videoRef.current.pause();
            }
        } else {
            if (pAss === "Lanzarote23") {
                message.success("Success", onClose);
                // document.getElementById("down").click();
                setPassflag(true);
            } else {
                message.config({
                    top: "60px"
                });
                message.warning("Password is not correct!", 25, onClose);
            }
        }
    };

    useEffect(() => {
        const handleVideoEnded = () => {
            // navigate("/email");
        };

        const videoElement = videoRef.current;

        if (videoElement) {
            videoElement.addEventListener("ended", handleVideoEnded);
        }

        return () => {
            if (videoElement) {
                videoElement.removeEventListener("ended", handleVideoEnded);
            }
        };
    }, [navigate]);

    const MoveEmail = () => {
        // navigate("/email");
    };

    return (
        <div className="bg-gray-800 mt-5">
            <Timer />
            <div className="h-[92vh] text-white bg-gray-800" >
                <Modal
                    title="..."
                    centered
                    okButtonProps={{ style: { backgroundColor: "black" } }}
                    cancelButtonProps={{ style: { backgroundColor: "black", color: "white", border: "none" } }}
                    cancelText="Close"
                    open={vIdeoflag}
                    onOk={ConfirmPassword}
                    onCancel={() => {
                        setVideoflag(false);
                        document.getElementById('downloaded-video').pause();
                    }}
                >
                    {pAssflag ? (
                        <ReactPlayer
                            url="https://youtube.com/shorts/jxKPf6MPa-0?feature=share"
                            playing
                            controls
                            width="100%"
                            height="100%"
                        />
                        // <video autoPlay className="text-center" id="downloaded-video" controls>
                        //     <source src="https://youtu.be/N0SwCo0qku0" type="video/mp4" />
                        // </video>
                    ) : (
                        <Space direction="vertical">
                            <Input.Password placeholder="input password" onChange={(event) => HandleChange(event.target.value)} />
                        </Space>
                    )}
                    {/* <a href='http://localhost:5000/download_intro1' id="down" hidden>down</a> */}
                </Modal>

                <div className='text-center mt-48 text-white'>
                    <p className='text-3xl mb-3 tracking-wide'>CONGRATULATIONS!</p>
                    <h4 className='mb-5 tracking-wide'>You've cracked the code on the tablet.</h4>
                    <h4 className='mt-10 tracking-wide'>To get more instructions, download the file</h4>
                    <Button type="primary" onClick={onDownload} style={{ backgroundColor: '#313030' }} className="mt-5 text-white">
                        Click Here to Download the PDF
                    </Button>
                </div>
            </div >
        </div>
    )
}

export default Down;
